import { ChangeDetectorRef, Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { Subscription } from 'rxjs';
import { IOptionsPsComplexDraftsReportsLoader } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-drafts-reports-loader/ps-complex-drafts-reports-loader.component.interfaces';
import { IOptionsPsComplexTermsAndConditionsExposed } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-terms-and-conditions/ps-complex-terms-and-conditions.component.interfaces';
import { PsDraftsReportComponent } from 'src/app/commonBussinessSRC/psComponents/ps-drafts-report/ps-drafts-report.component';
import { IOptionsPsDraftsReportComponent } from 'src/app/commonBussinessSRC/psComponents/ps-drafts-report/ps-drafts-report.component.interfaces';
import { IOptionsPsLabelExposed } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-interfaces';
import { PsCommonBusinessSettings } from 'src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings';
import { PsButtonNextComponent } from '../../psComponents/ps-base/ps-base-action/ps-action-button/ps-button-next/ps-button-next.component';
import { PsButtonPreviousComponent } from '../../psComponents/ps-base/ps-base-action/ps-action-button/ps-button-previous/ps-button-previous.component';
import { PsButtonSubmitComponent } from '../../psComponents/ps-base/ps-base-action/ps-action-button/ps-button-submit/ps-button-submit.component';
import { PsContainerStepperComponent } from '../../psComponents/ps-base/ps-base-container/ps-container-stepper/ps-container-stepper.component';
import { Events } from '../../psServices/Event/event.service';
import { ConstantCommon } from '../../psServices/models/common-constant';
import { CommonUtils } from '../../psServices/models/common-utils';
import { PsApplicationSettings } from '../../psServices/models/ps-app-settings';
import { IOptionsPsButtonApprove, IOptionsPsButtonNext, IOptionsPsButtonPrevious, IOptionsPsButtonReject, IOptionsPsButtonSave, IOptionsPsButtonSendBack, IOptionsPsButtonSubmit, IOptionsPsContainerFlip, IOptionsPsContainerStepperComponent, IOptionsTemplateScreen, IOptionsTemplateStepper, IPageCommon, PsCustomStepper } from '../../psServices/models/ps-common-interface';
import { PsCommonSettings } from '../../psServices/models/ps-common.settings';
import { PsTemplateBasePage } from '../ps-template-base/ps-template-base.page';
import { IOptionsPsActionIcon } from './../../psServices/models/ps-common-interface';



@Component({
  selector: 'ps-template-stepper',
  templateUrl: './ps-template-stepper.template.html',
  styleUrls: ['./ps-template-stepper.template.scss'],
})
export class PsTemplateStepper extends PsTemplateBasePage implements OnInit, OnDestroy {


  @Input() options: IOptionsTemplateStepper;
  // eslint-disable-next-line @angular-eslint/no-output-on-prefix
  @Output() onNextPreviousStepper: EventEmitter<any> = new EventEmitter<any>();
  @Output() draftLoaded: EventEmitter<any> = new EventEmitter<any>();
  @Output() onTemplateStepWidgetOpen: EventEmitter<any> = new EventEmitter<any>();
  @Output() onTemplateStepWidgetInit: EventEmitter<any> = new EventEmitter<any>();

  @ViewChild('next', { static: false }) next: PsButtonNextComponent;
  @ViewChild('previous', { static: false }) previous: PsButtonPreviousComponent;
  @ViewChild('submit', { static: false }) submit: PsButtonSubmitComponent;
  @ViewChild('report', { static: false }) report: PsDraftsReportComponent;
  @ViewChild('containerStepper', { static: false }) containerStepper: PsContainerStepperComponent;

  public stepper: PsCustomStepper;
  public isCheckerAndReportSelected = false;
  screenOptions: IOptionsTemplateScreen;
  public isMakerAndReportSelected = false;
  public isDraft = false;
  stepperOptions: IOptionsPsContainerStepperComponent = {};
  isCorporateMakerChecker: boolean

  psContainerFlip: IOptionsPsContainerFlip = {
    hideVisibleArea: false,
    isFlipped: false
  };

  previousIconOptions: IOptionsPsActionIcon = {
    iconName: 'arrow-round-back'
  };
  nextIconOptions: IOptionsPsActionIcon = {
    iconName: 'arrow-round-forward',

  };
  previousOptions: IOptionsPsButtonPrevious = {
    labelKey: 'previous_key',
    group: this.options.group
  };

  hintOptions: IOptionsPsLabelExposed = {
    labelKey: 'complete_all_fields_key'
  }

  nextOptions: IOptionsPsButtonNext = {
    labelKey: 'next_key',
    // stepper: this.stepper, // Commented by Richie for TP# 1105083: stepper declared before initialized
    group: this.options.group
  };


  submitOptions: IOptionsPsButtonSubmit = {
    labelKey: 'submit_key',
    group: this.options.group,
    emitClickOnFailure: true
  };

  saveAsDraftOptions: IOptionsPsButtonSave = {
    labelKey: 'save_key',
    group: this.options.group
  };
  saveAsDraftWidgetOptions: IOptionsPsButtonSave = {
    labelKey: 'save_and_quit_key',
    group: this.options.group
  };
  approveOptions: IOptionsPsButtonApprove = {
    labelKey: 'approve_key',
    group: this.options.group
  };
  rejectOptions: IOptionsPsButtonReject = {
    labelKey: 'reject_key',
    group: this.options.group
  };
  sendBackOptions: IOptionsPsButtonSendBack = {
    labelKey: 'send_back_key',
    group: this.options.group
  };
  termsAndConditionsOptions: IOptionsPsComplexTermsAndConditionsExposed = {
    checkBoxOptions: {
      group: this.options.group,
      fcName: 'checkboxConfirm',
      labelKey: 'agree_terms_and_conditions_key',
      //applyValueValidation: true // Added by ghada, tp#1383050  a custom validator will be applied to validate if component's value if true to mark it as valid
      allowDefaultValue: false //Added by Marina jira-AZDB-1294; terms and conditions shouldn't have a false value it is either empty or true 
    },
    htmlViewerOptions: {
    }
  };


  draftLoaderOptions: IOptionsPsComplexDraftsReportsLoader = {

  };
  readOnlyMode = false;


  public showReport = false;
  public showReportFlag = false;
  headerIconOptions: IOptionsPsActionIcon = {
    iconName: 'bookmark'
  };
  previewLblOptions = {
    labelKey: CommonUtils.translate('send_back_reason_key'),
    psClass: 'ps-lbl-preview',
    previewMode: true,
  };
  previewValueOptions = {
    labelKey: 'test',
    psClass: 'ps-value-preview',
    previewMode: true
  };
  draftsOptions: IOptionsPsDraftsReportComponent = {

  };
  eventSub: Subscription; // Added by Richie #TP 1105083 due to migration to ionic 5 since Event service was removed and replaced with observable
  navParam;
  isCorporate = false;
  showSendBackReason = false;
  hideStepper = false;
  quickMenu;
  constructor(private ref: ChangeDetectorRef, public events: Events) {
    super();
    this.eventSub = events.subscribe('draft:navigation', (requestObject) => {
      // user and time are the same arguments passed in `events.publish(user, time)`
      this.commonProv.copyObject(this.options.requestObject, requestObject, true, false);
      if (this.options.requestObject) {
        this.saveAsDraftOptions.dataSaveId = requestObject.dataSaveId;
        this.updateReferecnce();
      }
      this.checkMakerChecker();
      this.isDraft = false;
      if (this.options.requestObject) {
        if (this.options.requestObject.status === 'DR') {
          this.isDraft = true;
        }
      }
    });
  }
  stepperInit(stepper) {
    this.options.stepperInstance = this;
    this.stepper = stepper;
    this.nextOptions.stepper = this.stepper;
    this.submitOptions.stepper = this.stepper;
    this.saveAsDraftOptions.stepper = this.stepper;
    this.previousOptions.stepper = this.stepper;
    this.rejectOptions.stepper = this.stepper;
    this.sendBackOptions.stepper = this.stepper;
    this.approveOptions.stepper = this.stepper;
    this.draftsOptions.stepper = this.stepper;
    if (this.options.requestObject && this.options.requestObject[ConstantCommon.LAST_ACTIVE_STEP]) {
      this.stepper[ConstantCommon.LAST_ACTIVE_STEP] = this.options.requestObject[ConstantCommon.LAST_ACTIVE_STEP];
    }
  }

  ngOnInit() {
    this.quickMenu = PsApplicationSettings.CLIENT_ASSETS_CONFIG.UI_CONFIGURATION.BOTTOM_QUICK_MENU;
    this.screenOptions = {
      quickMenuCollapsed: true
    }
    if (PsCommonBusinessSettings.isCorporate) {
      this.isCorporate = true;
    }
    this.navParam = this.navService.getAllParams();
    const readOnly = this.navService.getParamKey('readOnlypage');
    const showTcAndPreviewSteps = this.navService.getParamKey('showTcAndPreviewInReadonly');
    if (readOnly != undefined) {
      this.readOnlyMode = readOnly;
    }
    this.options.showReport = this.options.showReport != undefined ? this.options.showReport : true;
    this.hideStepper = this.options.hideStepper ? this.options.hideStepper : false; // required to hide the stepper on payment page when the multi transfers loads it dynamically.
    this.options.type = this.options.type != undefined ? this.options.type : ''; // used for the preview template
    const screenVO = this.navService.getParamKey('ScreenVO');
    if (screenVO != undefined) {
      this.commonProv.copyObject(this.options.requestObject, screenVO, true, false);
      if (screenVO.sendBackReason != null && screenVO.status == 'PE' && PsCommonBusinessSettings.isMaker) {
        this.previewValueOptions.labelKey = screenVO.sendBackReason;
        this.showSendBackReason = true;
      }
      this.checkMakerChecker();
    }
    this.stepperOptions = this.options ? this.options : this.stepperOptions;
    this.stepperOptions.readonlyMode = readOnly;
    this.stepperOptions.showTcAndPreviewInReadonly = showTcAndPreviewSteps;
    this.commonProv.copyObject(this.submitOptions, this.options.submitOptions, false, true);
    this.commonProv.copyObject(this.saveAsDraftOptions, this.options.submitOptions, false, true);
    this.commonProv.copyObject(this.saveAsDraftWidgetOptions, this.options.submitOptions, false, true);
    this.commonProv.copyObject(this.sendBackOptions, this.options.submitOptions, false, true);
    this.commonProv.copyObject(this.approveOptions, this.options.submitOptions, false, true);
    this.commonProv.copyObject(this.rejectOptions, this.options.submitOptions, false, true);
    this.commonProv.copyObject(this.nextOptions, this.options, false, true);
    this.commonProv.copyObject(this.draftsOptions, this.options, false, true);
    this.draftsOptions.requestObject = this.options.requestObject; //added by Marina TP:#1583639 to have the same reference of requestObject in both components; ps-template-stepper and ps-drafts-report
    if (this.options.widgetStepper) {
      this.saveAsDraftOptions.saveProcedureFlow = true; // GA: Added this flag for the widget based form
      this.saveAsDraftWidgetOptions.saveProcedureFlow = true; // GA: Added this flag for the widget based form
    }

    this.termsAndConditionsOptions.checkBoxOptions.group = this.options.group;
    this.termsAndConditionsOptions.htmlViewerOptions.fileName = PsCommonSettings.oper_ID + "-TermsAndConditions.html";
    this.termsAndConditionsOptions.htmlViewerOptions.defaultFileName = "TermsAndConditions.html";
    this.termsAndConditionsOptions.htmlViewerOptions.parseHtmlFromFile = true;

    if (this.hideStepper) {
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.termsAndConditionsOptions.checkBoxOptions.fcName], 0);
    } else {
      this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.termsAndConditionsOptions.checkBoxOptions.fcName], 1);
    }
    if (!this.options.requestObject) {
      this.options.requestObject = {};
    }
    if (this.options.requestObject) {
      if (this.options.requestObject.status === 'DR') {
        this.isDraft = true;
      }
    }
    this.saveAsDraftOptions.dataSaveId = this.options.requestObject.dataSaveId;
    this.commonProv.setFormData(this.options.group, this.options.requestObject);
    if (this.navParam.JBPM) {
      this.initJBPMFlow();
    }
    // AZDB-7617 - AMANAOI250044 - 201435 - The system is displaying Reject, Send back, and Approve buttons under EditPay favorite screens - Dev => Muzammil Arif
    this.isCorporateMakerChecker = this.isCorporate && PsCommonBusinessSettings.isMaker && PsCommonBusinessSettings.isChecker;

  }

  checkMakerChecker() {
    if (PsCommonBusinessSettings.isAgent || PsCommonBusinessSettings.isCorporate) {
      if (PsCommonBusinessSettings.isChecker) {
        this.isCheckerAndReportSelected = true;
      }
      if (PsCommonBusinessSettings.isMaker) {
        this.isMakerAndReportSelected = true;
      }
    }
  }

  onFooterClick() {
    if (document.getElementsByTagName('ion-modal')[0] !== undefined) {
      document.getElementsByTagName('ion-modal')[0].dismiss();
    }
  }

  onNextAndPrevious(stepper) {
    this.onNextPreviousStepper.emit(stepper);
  }

  triggerNext(event) {
    if (this.next) {
      this.next.onNextClicked(event);
    }
  }

  triggerPrevious(event) {
    if (this.previous) {
      this.previous.onBackClicked(event);
    }
  }

  loadDrafts(report, event?: Event) {
    if (event) {
      event.preventDefault();
      event.stopPropagation();
    }

    this.checkDraftPage();

    if (report) { // report was chosen
      this.saveAsDraftOptions.dataSaveId = this.options.requestObject.dataSaveId;
      this.saveAsDraftOptions.labelKey = 'update_draft_key';
      if (PsCommonBusinessSettings.isAgent || PsCommonBusinessSettings.isCorporate) {
        if (PsCommonBusinessSettings.isChecker) {
          this.isCheckerAndReportSelected = true;
        }
        if (PsCommonBusinessSettings.isMaker) {
          this.isMakerAndReportSelected = true;
        }
      }
      this.draftLoaded.emit(report);
    }
    // this.ref.detectChanges();
  }

  checkDraftPage(forceClose?: boolean) {
    let reportStatus = !this.showReport;
    if (forceClose) {
      reportStatus = false;
    }
    this.showReportFlag = reportStatus;
    let animationDuration;
    if (!reportStatus) {
      if (this.report) {
        animationDuration = parseFloat(CommonUtils.getElementStyle(this.report.hostElement.nativeElement, 'animationDuration')) * 1000;
        setTimeout(() => {
          this.showReport = reportStatus;
        }, animationDuration);
        this.saveAsDraftOptions.dataSaveId = null;
        this.saveAsDraftOptions.labelKey = 'save_key';
      }
    } else {
      this.showReport = reportStatus;
    }
    if (this.options.requestObject) {
      if (this.options.requestObject.actionType === 'save') {
        this.isDraft = true;
      }
    }
  }

  ngOnDestroy() {
    // Commented/Added by Richie #TP 1105083
    // this.events.unsubscribe('draft:navigation');
    if (this.eventSub) {
      this.eventSub.unsubscribe();
    }
    this.onTemplateStepWidgetOpen.unsubscribe();
    this.onTemplateStepWidgetInit.unsubscribe();
    // End Richie
  }

  initJBPMFlow() {
    this.options.requestObject.completeBpmTask = true;
    this.options.requestObject.JBPM = this.navParam.JBPM;
    this.commonProv.copyObject(this.options.requestObject, this.options.requestObject.JBPM.taskInfo, false, false);

    const newParams = CommonUtils.createBPMParams(this.navParam.JBPM.screenMarkup.queryParams, null, this.commonProv.session, 'in');
    this.commonProv.copyObject(this.options.requestObject, newParams, false, false);
    if (CommonUtils.isNotNull(newParams.readOnlypage)) {
      this.readOnlyMode = newParams.readOnlyMode;
    }

  }
  // edited by Marina TP:#1586190; pass nextPageOtions to the report page or open it directly in case no report to be opened
  afterSubmitAction(response) {
    if (this.navParam.JBPM) {
      // revertBack partial revert
      this.navParam.postCallFunction.func({ ...this.navParam.JBPM, bpmParametersMap: { amount_out: this.options.requestObject.transactionAmount } });
    } else {
      const currentPageProfile = this.commonProv.getPageByOperId(response.operId);
      if (response.dontShowReport !== 'true' && response.dontShowReport !== true) {
        if ((currentPageProfile?.PROG_REF !== undefined && (response.outputType === 'S' || response.outputType === 'I')) || (currentPageProfile?.FAILURE_PROG_REF !== undefined && response.outputType === 'F')) {
          let page: IPageCommon = {
            icon: 'document',
            title: 'submission_report_key',
            component: 'report-page',
            operID: response.operId,
            param: { ...this.options.requestObject, ...response.commonResponseList, outputType: response.outputType, nextPage: this.options.nextPageOptions }
          };
          this.navService.openPage(page);
        }
        else if (this.options.nextPageOptions) {
          this.navService.openPage(this.options.nextPageOptions);
        }
      }
      else if (this.options.nextPageOptions) {
        this.navService.openPage(this.options.nextPageOptions);
      }
    }
  }

  updateReferecnce() {
    if (this.saveAsDraftWidgetOptions.dataSaveId) {
      this.saveAsDraftOptions.dataSaveId = this.saveAsDraftWidgetOptions.dataSaveId;
    }

    if (this.saveAsDraftOptions.dataSaveId) {
      this.saveAsDraftWidgetOptions.dataSaveId = this.saveAsDraftOptions.dataSaveId;
    }
    if (this.options.widgetStepper) {
      setTimeout(() => {
        this.containerStepper.updateAllWidgetsStatus();
      }, 1000);
    }
  }

  returnSubmitOptions(numberOfValid) {
    if (this.options.widgetStepper) {
      if (CommonUtils.isNull(this.submitOptions.psClass)) {
        this.submitOptions.psClass = '';
      }
      if (numberOfValid < 1) {
        if (this.submitOptions.psClass.indexOf('disableContent') < 0) {
          this.submitOptions.psClass = 'disableContent ' + this.submitOptions.psClass
        }
      } else {
        this.submitOptions.psClass = this.submitOptions.psClass.replace('disableContent', '');
      }
    }
    // added by Marina jira-AZDB-2518; to update navigateBack property
    this.submitOptions.enableNavigateBack = this.options.submitOptions.enableNavigateBack ? this.options.submitOptions.enableNavigateBack : true;
    return this.submitOptions;
  }

  onStepWidgetOpenEvent(event) {
    this.onTemplateStepWidgetOpen.emit(event)
  }

  onStepWidgetInitEvent(event){
    this.onTemplateStepWidgetInit.emit(event)
  }

}
